<template lang='pug'>
#app.container.mt-5
  h4 使用 DownloadFile 组件
  h5 默认样式，适用情景：返回接口，服务器动态生成文件，并直接返回二进制文件
  download-file(useBlob btnText='组件模式 | 通过url下载文件', @download='exportUseComponent')
  hr
  h5 默认样式，适用情景：静态资源存储在第三方oss上，请求后台返回动态url的情况，根据后台返回的url下载文件
  download-file(btnText='组件模式 | 使用blob导出二进制文件', @download='downloadUseComponent')
  hr
  h5 利用 slot-scope 自定义样式
  download-file(@download='downloadUseComponent')
    template(slot-scope="downloadBtn")
      a(href='#', @click.prevent='downloadBtn.download') 下载文件
  hr.mb-5
  h5 不使用组件，直接使用实例的方法
  button.btn.btn-outline-primary.mr-5(type='button', @click='downloadUseVueMethods') 实例方法 | 通过url下载文件
  button.btn.btn-outline-primary(type='button', @click='exportUseVueMethods') 实例方法 | 使用blob导出二进制文件
</template>

<script>
import 'bootstrap/dist/css/bootstrap.min.css'

let url = 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png'
let mockResponse = {
  headers: {
    'content-disposition': 'attachment;filename="test.txt"'
  },
  data: 'test, use blob object download file'
}

export default {
  name: 'app',
  methods: {
    downloadUseComponent (callback) { // 组件模式 | 通过url下载文件
      setTimeout(() => {
        callback(url, 'helloWorld')
      }, 1000)
    },
    exportUseComponent (callback) { // 组件模式 | 使用blob导出二进制文件
      setTimeout(() => {
        callback(mockResponse)
      })
    },
    downloadUseVueMethods() { // 实例方法 | 通过url下载文件
      setTimeout(() => {
        this.$downloadFileByUrl(url)
      }, 1000)
    },
    exportUseVueMethods() { // 实例方法 | 使用blob导出二进制文件
      setTimeout(() => {
        this.$exportFile(mockResponse)
      })
    }
  }
}
</script>
